

@import url("https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@200;400&display=swap");

* {
  font-family: "Josefin Sans", sans-serif;
  margin:0;
}


.partition {
  display: flex;
  height: 40px;
  width: 100%;
  padding: 0 0px;
}
.ul{
  display: flex;
  justify-items: center;
  margin-left: 0%;
}
.floatleft>
.floatleft,.li{
  padding-left: 10px;
}
.li{
  list-style: none;
  padding-right: 90px;
  color:rgb(9, 27, 2);
  font-weight: bold;
}
.topp{
  width: 100%;
  height: 40px;
}
.space{
  height: 50px;
  display: flex;
  justify-content: center;
  width: 100%;
  border-radius: 10px;
  background: linear-gradient(
    90deg,
    rgb(200, 231, 210) 0%,
    rgb(1, 49, 20) 100%
  );
 
}
.bottom{
  
  display: flex;
  min-height: 1400px;

}
.social{
  margin-right: 200px;
  display: flex;
  padding-right: 10px;
  justify-content: space-evenly;
}

.left{
 
border-right:inset;
flex:1;
text-align: center;



}

.center{
  border-right:inset;
  flex:1;
  text-align: center;
}

.right{
  
  flex:1;
  text-align: center;
}

