@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@200;400&display=swap);


* {
  font-family: "Josefin Sans", sans-serif;
  margin:0;
}


.partition {
  display: -webkit-flex;
  display: flex;
  height: 40px;
  width: 100%;
  padding: 0 0px;
}
.ul{
  display: -webkit-flex;
  display: flex;
  justify-items: center;
  margin-left: 0%;
}
.floatleft>
.floatleft,.li{
  padding-left: 10px;
}
.li{
  list-style: none;
  padding-right: 90px;
  color:rgb(9, 27, 2);
  font-weight: bold;
}
.topp{
  width: 100%;
  height: 40px;
}
.space{
  height: 50px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  width: 100%;
  border-radius: 10px;
  background: linear-gradient(
    90deg,
    rgb(200, 231, 210) 0%,
    rgb(1, 49, 20) 100%
  );
 
}
.bottom{
  
  display: -webkit-flex;
  
  display: flex;
  min-height: 1400px;

}
.social{
  margin-right: 200px;
  display: -webkit-flex;
  display: flex;
  padding-right: 10px;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}

.left{
 
border-right:inset;
-webkit-flex:1 1;
        flex:1 1;
text-align: center;



}

.center{
  border-right:inset;
  -webkit-flex:1 1;
          flex:1 1;
  text-align: center;
}

.right{
  
  -webkit-flex:1 1;
  
          flex:1 1;
  text-align: center;
}


.searchInputs {
  margin-top: 105px;
  display: -webkit-flex;
  display: flex;
}

.search input {
  background-color: white;
  border: 0;
  border-radius: 2px;
  border-top-right-radius: 0px;
  border-bottom-right-radius: 0px;
  font-size: 18px;
  padding: 15px;
  height: 30px;
  width: 300px;
}

.searchIcon {
  height: 60px;
  width: 50px;
  background-color: white;
  display: grid;
  place-items: center;
}

input:focus {
  outline: none;
}
.searchIcon svg {
  font-size: 35px;
}

.dataResult {
  margin-top: 5px;
  width: 300px;
  height: 200px;
  background-color: white;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  overflow: hidden;
  overflow-y: auto;
}

.dataResult::-webkit-scrollbar {
  display: none;
}

.dataResult .dataItem {
  width: 100%;
  height: 50px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  color: black;
}

.dataItem p {
  margin-left: 10px;
}
a {
  text-decoration: none;
}

a:hover {
  background-color: lightgrey;
}

#clearBtn {
  cursor: pointer;
}

